import React from 'react';
import { connect } from 'react-redux';

import { withLocalize, Translate } from "react-localize-redux";
import siteTranslations from "../translations/site.json";

import { MuiThemeProvider } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';
import TextField from '@material-ui/core/TextField/TextField';
import Button from '@material-ui/core/Button/Button';

import imgScroll from '../images/scroll.png';
import imgDragonWarrior from '../images/dragonwarrior.png';
import imgSS from '../images/soulstone.png';

import AOS from 'aos';
import 'aos/dist/aos.css';

import { showSnackbar } from '../actions/snackbar';

import customTheme from './customTheme';

import { SCREEN_MY_HEROES, setScreen } from '../actions/app';

import TronWeb from 'tronweb';
import Tron from '../utils/tron';

const FOUNDATION_ADDRESS = 'TMiT1foMn3D9TysDXeZqVM5Fk4Pbwvfa2d';

AOS.init({
  once: true, // whether animation should happen only once - while scrolling down
  mirror: false, // whether elements should animate out while scrolling past them
});

class PreSale extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      tronWeb: null,
      game: 1,
      sendAmount: '1',
      scrollType: '0',
      isLoading: false,
    };
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {

    const { params } = this.props;

    console.log("params",params);

    // tronweb

    await new Promise(resolve => {
      const tronWebState = {
        installed: !!window.tronWeb,
        loggedIn: window.tronWeb && window.tronWeb.ready,
      };

      if (tronWebState.installed) {
        this.setState({
          tronWeb: tronWebState,
        });

        return resolve();
      }

      let tries = 0;

      const timer = setInterval(() => {
        if (tries >= 10) {
          const TRONGRID_API = 'https://api.shasta.trongrid.io';

          window.tronWeb = new TronWeb(
            TRONGRID_API,
            TRONGRID_API,
            TRONGRID_API
          );

          this.setState({
            tronWeb: {
              installed: false,
              loggedIn: false,
            },
          });

          clearInterval(timer);
          return resolve();
        }

        tronWebState.installed = !!window.tronWeb;
        tronWebState.loggedIn = window.tronWeb && window.tronWeb.ready;

        if (!tronWebState.installed) return tries++;

        this.setState({
          tronWeb: tronWebState,
        });

        resolve();
      }, 100);
    });

    if (!this.state.tronWeb.loggedIn) {
      // Set default address (foundation address) used for contract calls
      // Directly overwrites the address object as TronLink disabled the
      // function call
      window.tronWeb.defaultAddress = {
        hex: window.tronWeb.address.toHex(FOUNDATION_ADDRESS),
        base58: FOUNDATION_ADDRESS,
      };

      window.tronWeb.on('addressChanged', () => {
        if (this.state.tronWeb.loggedIn) return;

        this.setState({
          tronWeb: {
            installed: true,
            loggedIn: true,
          },
        });
      });
    }

    await Tron.setTronWeb(window.tronWeb);
    const globalData = await Tron.getGlobalData();
    this.setState({ globalClaimedDividend: globalData[0] });
    this.setState({ globalReferral: globalData[1] });
  }

  loadAssets = async activeAccount => {};

  componentDidUpdate(prevProps) {
    // Typical usage (don't forget to compare props):
    if (
      (this.props.activeAccount !== prevProps.activeAccount ||
        this.props.reloadAccountNonce !== prevProps.reloadAccountNonce) &&
      this.props.activeAccount
    ) {
      this.loadAssets(this.props.activeAccount);
    }
  }

  showAccounts = async event => {
    const { showAccounts } = this.props;
    showAccounts(event.currentTarget);
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  roll1 = () => {
    this.setState({ sendAmount: '1' });
  };
  roll10 = () => {
    this.setState({ sendAmount: '10' });
  };
  roll50 = () => {
    this.setState({ sendAmount: '50' });
  };
  roll100 = () => {
    this.setState({ sendAmount: '100' });
  };

  scrollCommon = () => {
    this.setState({ scrollType: '0' });
  };
  scrollRare = () => {
    this.setState({ scrollType: '1' });
  };
  scrollEpic = () => {
    this.setState({ scrollType: '2' });
  };
  scrollLegendary = () => {
    this.setState({ scrollType: '3' });
  };

  sunToTrx(sun) {
    if (!sun) {
      return '';
    }
    return sun / 1000000;
  }

  purchaseScroll = async () => {
    const { sendAmount,scrollType } = this.state;
    const { setScreen, showSnackbar } = this.props;
    await Tron.purchase(sendAmount, scrollType, 'TMiT1foMn3D9TysDXeZqVM5Fk4Pbwvfa2d')
      .then(function(data) {
        showSnackbar('Check your heroes!');
        setScreen(SCREEN_MY_HEROES);
      })
      .catch(function(err) {
        console.log(err)
        showSnackbar(err);
      });
  };

 

  render() {
    const { globalClaimedDividend, globalReferral } = this.state;

    const { sendAmount, scrollType } = this.state;

    // let installed = tronWeb ? tronWeb.installed : null;
    // let loggedIn = tronWeb ? tronWeb.loggedIn : null;
    // console.log(installed)
    // console.log(loggedIn)

    const amount = parseInt(sendAmount, 10);
    let btn1Class = amount === 1 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btn10Class =
      amount === 10 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btn50Class =
      amount === 50 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btn100Class =
      amount === 100 ? 'glowing-blue medium-text' : 'glowing-silver';

    const scroll = parseInt(scrollType, 10);
    let btnCommonClass = scroll === 0 ? 'glowing-green medium-text' : 'glowing-silver';
    let btnRareClass =
      scroll === 1 ? 'glowing-blue medium-text' : 'glowing-silver';
    let btnEpicClass =
      scroll === 2 ? 'glowing-pink medium-text' : 'glowing-silver';
    let btnLegendaryClass =
      scroll === 3 ? 'glowing-gold medium-text' : 'glowing-silver';
    let basePrice = 50;
    let stonePrice = 50;
    let buyText = "presale.scrollcommon";
    if(scroll == 0)
    { 
      buyText = "presale.scrollcommon";
      basePrice = 50;
    }else if(scroll == 1)
    {
      buyText = "presale.scrollrare";
      basePrice = 500;
    }else if(scroll == 2)
    {
      buyText = "presale.scrollepic";
      basePrice = 10000;
    }else if(scroll == 3)
    {
      buyText = "presale.scrolllegendary";
      basePrice = 100000;
    }

   let cost = sendAmount * basePrice;
   let stoneNumber = cost/stonePrice;

    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="home">
          <div className="cards">
            <div color="primary" className="card home-card">
              <Typography variant="h4" color="secondary">
               
                <span className="glowing-pink white" id="trigger-scroll">
                  <h1>
                    <Translate id="presale.title" />
                  </h1>
                  <Translate id="presale.subtitle" />
                </span>
              </Typography>
              <div className="dice-slider">
                <Grid container spacing={24}>
                  <Grid item xs={12} md={12} lg={12}>
                    <span>
                      <span className="large-text glowing-green">30</span>{' '}
                      <Translate id="presale.description1" />,{' '} <img
                          src={imgSS}
                          alt=<Translate id="general.soulstone" />
                          className="image-ss-medium"
                        />
                      <span className="glowing-gold"><Translate id="general.soulstone" /></span> {' '}
                      <Translate id="general.dividends" />,{' '}

                      <Translate id="presale.limitedtime" /> <span className="glowing-red">50%</span> <Translate id="presale.discount" />

                    </span>
                  </Grid>
                </Grid>
                <div className="space" />
                <Grid container spacing={24}>
                  <Grid item xs={12} md={12} lg={12}>
                    <Translate id="presale.globaldividends" />:{' '}
                    <span className="large-text glowing-blue">
                      {this.sunToTrx(globalClaimedDividend)} TRX
                    </span>
                  </Grid>
                  <Grid item xs={12} md={12} lg={12}>
                    <Translate id="presale.globalreferrals" />:{' '}
                    <span className="large-text glowing-blue">
                      {this.sunToTrx(globalReferral)} TRX
                    </span>
                  </Grid>
                </Grid>
              </div>

              <div className="card send-card">
                <Grid container spacing={24}>
                  <Grid item xs={12} md={6} lg={6}>
                    <div
                      className="image-placeholder"
                      data-aos="zoom-in"
                      data-aos-delay="50"
                      data-aos-anchor="trigger-scroll"
                      data-aos-anchor-placement="top-center"
                    >
                      <img
                        src={imgScroll}
                        alt="Get Hero"
                        className="image-scroll animate-flicker"
                      />
                    </div>
                  </Grid>
                  <Grid item xs={12} md={6} lg={6}>
                    <div className="buy-placeholder">
                      <div>
                        <span className="large-text glowing-blue">
                          {cost} TRX{' '}
                        </span>
                      </div>
                      <div className="space" />

                      <div><Translate id={buyText} /></div>

                      <div className="space" />
                      <div>
                        <Translate id="presale.alsorewards" />
                        <img
                          src={imgSS}
                          alt=<Translate id="general.soulstone" />
                          className="image-ss-small"
                        />
                        <span className="glowing-gold">
                          x{stoneNumber} <Translate id="general.soulstone" />
                        </span>
                      </div>
                      <div className="space" />
                      <div className="hidden">
                        <TextField
                          required
                          name="sendAmount"
                          label="Get Heroes"
                          className="send-token"
                          value={sendAmount}
                          margin="normal"
                          type="number"
                          placeholder="0.00"
                        />
                        <TextField
                          required
                          name="scrollType"
                          label=""
                          className="send-token"
                          value={scrollType}
                          margin="normal"
                          type="number"
                          placeholder="0.00"
                        />
                      </div>
                      <Grid container spacing={0}>
                        <Grid item xs={12} md={12} lg={12}>
                          <Button
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.scrollCommon}
                          >
                            <span className={btnCommonClass}><Translate id="general.common" /></span>
                          </Button>
                          <Button
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.scrollRare}
                          >
                            <span className={btnRareClass}><Translate id="general.rare" /></span>
                          </Button>
                          <Button
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.scrollEpic}
                          >
                            <span className={btnEpicClass}><Translate id="general.epic" /></span>
                          </Button>

                          <Button
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.scrollLegendary}
                          >
                            <span className={btnLegendaryClass}><Translate id="general.legendary" /></span>
                          </Button>
                        </Grid>
                      </Grid>
                      <Grid container spacing={0}>
                        <Grid item xs={12} md={12} lg={12}>
                          <Button
                            id="roll-min-button"
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.roll1}
                          >
                            <span className={btn1Class}>1</span>
                          </Button>
                          <Button
                            id="roll-2x-button"
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.roll10}
                          >
                            <span className={btn10Class}>10</span>
                          </Button>
                          <Button
                            id="roll-2x-button"
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.roll50}
                          >
                            <span className={btn50Class}>50</span>
                          </Button>

                          <Button
                            id="roll-max-button"
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                            onClick={this.roll100}
                          >
                            <span className={btn100Class}>100</span>
                          </Button>
                        </Grid>
                      </Grid>

                      <Grid container spacing={24}>
                        <Grid item xs={12} md={12} lg={12}>
                          <Button
                            id="send-token-button"
                            className="actionbutton"
                            variant="contained"
                            color="secondary"
                            size="large"
                            onClick={this.purchaseScroll}
                            disabled={this.isSending()}
                          >
                            <Translate id="presale.buyscroll" />
                          </Button>
                        </Grid>
                      </Grid>
                    </div>
                  </Grid>
                </Grid>
              </div>
              <div className="card send-card" id="trigger-intro">
                <Grid container spacing={24}>
                  <Grid item xs={12} md={6} lg={6}>
                    <div className="buy-placeholder">
                      <div>
                        <span className="large-text glowing-pink">
                          <Translate id="general.tronlegend" />
                        </span>
                      </div>
                      <div className="space" />
                      <div>
                        <span className="glowing-pink">
                          <Translate id="general.tronlegend" /></span> <Translate id="presale.intro1" />.
                      </div>
                      <div>
                        <Translate id="presale.intro2" />.
                      </div>
                      <div>
                        <Translate id="presale.intro3" />{' '}
                        <span className="glowing-blue">
                          <Translate id="presale.intro4" />
                        </span>{' '}
                        <Translate id="presale.intro5" />.
                      </div>
                      <div>
                      <span className="glowing-red large-text"><Translate id="presale.intro6" /></span>
                      </div>

                    </div>
                  </Grid>
                  <Grid item xs={12} md={6} lg={6}>
                    <div
                      className="image-placeholder"
                      data-aos="zoom-in"
                      data-aos-delay="50"
                      data-aos-anchor="trigger-intro"
                      data-aos-anchor-placement="top-center"
                    >
                      <img
                        src={imgDragonWarrior}
                        alt="Non Fungible Token"
                        className="image-scroll"
                      />
                    </div>
                  </Grid>
                </Grid>
              </div>
            </div>
          </div>
        </div>
        <div />
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = {
  showSnackbar,
  setScreen,
};

PreSale.propTypes = {
  // classes: PropTypes.object.isRequired,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(PreSale));
