import { RESET_STATE } from '../actions/constants';
import { SET_SCREEN, SCREEN_PRE_SALE } from '../actions/app';

const initialState = {
  screen: SCREEN_PRE_SALE,
};

export function appReducer(state = initialState, action) {
  switch (action.type) {
    case SET_SCREEN: {
      return {
        ...state,
        ...action.payload,
      };
    }
    case RESET_STATE:
      return initialState;
    default:
      return state;
  }
}
