import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import { withLocalize, Translate } from "react-localize-redux";
import siteTranslations from "../translations/site.json";

import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';
import TextField from '@material-ui/core/TextField/TextField';

import Button from '@material-ui/core/Button/Button';

import imgTron from '../images/tron-logo.png';
import imgSS from '../images/soulstone.png';
import { showSnackbar } from '../actions/snackbar';

import { setScreen } from '../actions/app';

import TronWeb from 'tronweb';
import Tron from '../utils/tron';

import presetHeroes from '../config/hero';

import AOS from 'aos';
import 'aos/dist/aos.css';

AOS.init();
const styles = {
  root: {
    width: '100%',
    padding: '30px 0 10px',
  },
  cssOutlinedInput: {
    width:500,
    color: '#fff',
    textShadow:
      'rgb(43, 0, 43) 0px 0px 5px, rgb(204, 0, 204) 0px 0px 20px, rgb(255, 0, 255) 0px 0px 10px',
    borderColor: '#fff',
  },
  cssOutlinedInputReadOnly: {
    color: '#fff',
    textShadow:
      'rgb(43, 0, 43) 0px 0px 5px, rgb(204, 0, 204) 0px 0px 20px, rgb(255, 0, 255) 0px 0px 10px',
    borderColor: '#fff',
    '&$cssFocused $notchedOutline': {
      borderColor: '#fff',
      textShadow:
        'rgb(43, 0, 43) 0px 0px 5px, rgb(204, 0, 204) 0px 0px 20px, rgb(255, 0, 255) 0px 0px 10px',
    },
  },
  notchedOutline: {
    borderColor: '#fff',
    color: '#fff',
  },
};

const FOUNDATION_ADDRESS = 'TMiT1foMn3D9TysDXeZqVM5Fk4Pbwvfa2d';

class MyAccount extends Component {
  constructor(props) {
    super(props);

    this.state = {
      purchases: null,
      totalHeroes: 0,
      heroes: [],
      cards: [],
      heroGroups: [],
      lastUpdatedTime: null,

      stones: 0,
      claimableDividend: 0,
      claimedDividend: 0,
      referral: 0,
    };
    this.props.addTranslation(siteTranslations);
  }


  async componentDidMount() {
    await this.initTronWeb();
    await this.loadCards();
    await this.loadPlayerData();
    window.addEventListener('load', AOS.refresh);
  }

  async initTronWeb() {
    // tronweb

    await new Promise(resolve => {
      const tronWebState = {
        installed: !!window.tronWeb,
        loggedIn: window.tronWeb && window.tronWeb.ready,
      };

      if (tronWebState.installed) {
        this.setState({
          tronWeb: tronWebState,
        });

        return resolve();
      }

      let tries = 0;

      const timer = setInterval(() => {
        if (tries >= 10) {
          const TRONGRID_API = 'https://api.shasta.trongrid.io';

          window.tronWeb = new TronWeb(
            TRONGRID_API,
            TRONGRID_API,
            TRONGRID_API
          );

          this.setState({
            tronWeb: {
              installed: false,
              loggedIn: false,
            },
          });

          clearInterval(timer);
          return resolve();
        }

        tronWebState.installed = !!window.tronWeb;
        tronWebState.loggedIn = window.tronWeb && window.tronWeb.ready;

        if (!tronWebState.installed) return tries++;

        this.setState({
          tronWeb: tronWebState,
        });

        resolve();
      }, 100);
    });

    if (!this.state.tronWeb.loggedIn) {
      // Set default address (foundation address) used for contract calls
      // Directly overwrites the address object as TronLink disabled the
      // function call
      window.tronWeb.defaultAddress = {
        hex: window.tronWeb.address.toHex(FOUNDATION_ADDRESS),
        base58: FOUNDATION_ADDRESS,
      };

      window.tronWeb.on('addressChanged', () => {
        if (this.state.tronWeb.loggedIn) return;

        this.setState({
          tronWeb: {
            installed: true,
            loggedIn: true,
          },
        });
      });
    }

    await Tron.setTronWeb(window.tronWeb);
  }

  getPlayerCardsGroup = async pages => {
    return new Promise(async resolve => {
      let promises = [];

      for (var i = 0; i < pages; ++i) {
        promises.push(this.getPlayerCardsByPage(i));
      }

      Promise.all(promises)
        .then(results => {
          console.log(results);

          let ids = [];
          let protos = [];
          let purities = [];

          for (let i = 0; i < results.length; i++) {
            let result = results[i];
            for (let j = 0; j < result.ids.length; j++) {
              ids.push(result.ids[j]);
            }
            for (let j = 0; j < result.protos.length; j++) {
              protos.push(result.protos[j]);
            }
            for (let j = 0; j < result.purities.length; j++) {
              purities.push(result.purities[j]);
            }
          }

          const heroes = {
            ids: ids,
            protos: protos,
            purities: purities,
          };

          this.setState({ heroes });

          resolve(true);
        })
        .catch(e => {
          resolve(true);
        });
    });
  };

  getPlayerCardsByPage = async page => {
    return new Promise(async resolve => {
      const cards = await Tron.getPlayerCards(page);
      resolve(cards);
    });
  };

  loadPlayerData = async () => {
    const playerData = await Tron.getPlayerData();

    this.setState({ stones: playerData[0] });
    this.setState({ claimableDividend: playerData[1] });
    this.setState({ claimedDividend: playerData[2] });
    this.setState({ referral: playerData[3] });
  };

  sunToTrx(sun) {
    if (!sun) {
      return 0;
    }
    return sun / 1000000;
  }

  loadCards = async () => {
    const totalCardsCount = await Tron.balanceOf(
      window.tronWeb.defaultAddress.base58
    );

    this.setState({ totalHeroes: totalCardsCount.toNumber() });

    let pages = totalCardsCount.toNumber() / 100;

    if (pages * 100 < totalCardsCount) {
      pages += 1;
    }
    console.log('pages ' + pages);

    await this.getPlayerCardsGroup(pages);
  };

  loadCardsGroup = async () => {
    const { heroes } = this.state;

    let cards = heroes;

    let { heroGroups } = this.state;
    if (cards) {
      const ids = cards.ids;
      const protos = cards.protos;
      const purities = cards.purities;
      for (let i = 0; i < ids.length; i++) {
        const id = ids[i].toNumber();
        const proto = protos[i];
        const purity = purities[i];

        const heroData = this.getHeroData(proto);
        const rarity = heroData.ra;

        let foundRarityIndex = -1;
        let foundHero = false;
        for (let j = 0; j < heroGroups.length; j++) {
          const heroGroup = heroGroups[j];
          if (heroGroup.rarity === rarity) {
            // found Rarity
            foundRarityIndex = j;
            if (heroGroup.heroes) {
              for (let k = 0; k < heroGroup.heroes.length; k++) {
                const hero = heroGroup.heroes[k];

                if (hero.proto === proto) {
                  // found
                  foundHero = true;

                  if (hero.bestPurity < purity) {
                    hero.bestPurity = purity;
                    hero.bestId = id;
                  }

                  hero.count += 1;
                }
              }
            }
          }
        }

        if (!foundHero) {
          //push card
          const hero = {
            proto: proto,
            bestPurity: purity,
            bestId: id,
            count: 1,
          };

          if (foundRarityIndex > -1) {
            // push to exisiting group
            const heroGroup = heroGroups[foundRarityIndex];
            heroGroup.heroes.push(hero);
          } else {
            const newheroes = [];
            newheroes.push(hero);

            const heroGroup = {
              rarity: rarity,
              heroes: newheroes,
            };
            heroGroups.push(heroGroup);
          }
        } // if hero not found
      } // for i loop
    } // if cards

    // this.setState({heroes});
    this.setState({ heroGroups });
  };

  claimDividend = async () => {
    const { showSnackbar } = this.props;
    await Tron.claimDividend()
      .then(function(data) {
        showSnackbar('Dividend claimed');
      })
      .catch(function(err) {
        showSnackbar('Err');
      });
  };

  isSending = () => {
    const { isLoading } = this.state;
    return !!isLoading;
  };

  getHeroData = id => {
    for (var i = 0; i < presetHeroes.length; i++) {
      if (presetHeroes[i].id === id) {
        return presetHeroes[i];
      }
    }
    return null;
  };

  copyLink= async() => {
    const { showSnackbar } = this.props;
    showSnackbar('Copied');
  };


  renderPlayerData() {

    const {  classes } = this.props;
    const { stones, claimableDividend, claimedDividend, referral } = this.state;

    let myReferralLink = "http://localhost:3000?r=" + window.tronWeb.defaultAddress.base58;
    return (
      <div className="home">
        <div className="cards">
          <div color="primary" className="card home-card">
            <Typography variant="h4" color="secondary">
              <span className="glowing-pink white"><Translate id="general.myaccount" /></span>
            </Typography>
            <div className="kingdom-placehoder">
              <Grid container spacing={24}>
                <Grid item xs={12} md={6} lg={6}>
                  <Grid container spacing={24} className="card">
                    <Grid item xs={12} md={12} lg={12}>
                      <span>
                        <Translate id="general.soulstone" />:{' '}
                        <span className="large-text glowing-gold">
                          {stones}
                        </span>{' '}
                        <img
                          src={imgSS}
                          alt=<Translate id="general.soulstone" />
                          className="image-ss-medium"
                        />
                      </span>
                    </Grid>

                    <Grid item xs={12} md={12} lg={12}>
                      <span>
                        <Translate id="myaccount.claimeddividend" />:{' '}
                        <span className="large-text glowing-blue">
                          {this.sunToTrx(claimedDividend)} TRX
                        </span>{' '}
                      </span>
                    </Grid>
                    <Grid item xs={12} md={12} lg={12}>
                      <span>
                        <Translate id="myaccount.claimedreferral" />:{' '}
                        <span className="large-text glowing-blue">
                          {this.sunToTrx(referral)} TRX
                        </span>{' '}
                      </span>
                    </Grid>

                    <Grid item xs={12} md={12} lg={12}>
                      <div>
                        <Translate id="myaccount.myreferrallink" /> (<Translate id="myaccount.intro1" />  <span className="glowing-pink">
                          10%
                        </span>{' '} <Translate id="myaccount.intro2" />)
                      </div>
                      <div>
                       
                       <TextField
                        
                        label=""
                        name="selectedAsset"
                        className="referral-link-box"
                        value={myReferralLink}
                        margin="normal"
                        color="secondary"
                        variant="outlined"
                        InputLabelProps={{
                          classes: {
                            root: classes.cssLabel,
                          },
                        }}
                        InputProps={{
                          classes: {
                            root: classes.cssOutlinedInput,
                            input: classes.cssOutlinedInputReadOnly,
                            notchedOutline: classes.notchedOutline,
                          }
                        }}
                      />
                      </div>
                      <div>
                        <CopyToClipboard text={myReferralLink}
                          onCopy={()=>{this.copyLink()}}>
                          <Button
                            className="roll-quick-button"
                            variant="contained"
                            size="small"
                            color="primary"
                          >
                            <span className="glowing-red"><Translate id="myaccount.copylink" /></span>
                          </Button>
                        </CopyToClipboard>
                      </div>
                    </Grid>

                    
                  </Grid>
                </Grid>

                <Grid item xs={12} md={6} lg={6}>
                  <div className="card">
                    <Grid container spacing={24}>
                      <Grid item xs={12} md={12} lg={12}>
                        <div>
                          <Translate id="myaccount.intro3" />{' '}
                          <span className="large-text glowing-blue">
                            {this.sunToTrx(claimableDividend)} TRX
                          </span>{' '}
                        </div>
                        <div className="space" />
                        <div
                          className="tron-logo-placeholder"
                          data-aos="zoom-in"
                          data-aos-delay="50"
                          data-aos-anchor-placement="top-center"
                        >
                          <img
                            src={imgTron}
                            alt=<Translate id="myaccount.claim" />
                            className="image-tron"
                          />
                        </div>
                      </Grid>
                      <Grid item xs={12} md={12} lg={12}>
                        <div className="buy-placeholder">
                          <Grid container spacing={24}>
                            <Grid item xs={12} md={12} lg={12}>
                              <Button
                                id="send-token-button"
                                className="actionbutton"
                                variant="contained"
                                color="secondary"
                                size="large"
                                onClick={this.claimDividend}
                                disabled={this.isSending()}
                              >
                                <Translate id="myaccount.claim" />
                              </Button>
                            </Grid>
                          </Grid>
                        </div>
                      </Grid>
                    </Grid>
                  </div>
                </Grid>
              </Grid>
            </div>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return this.renderPlayerData();
  }
}

const mapDispatchToProps = {
  setScreen,
  showSnackbar,
};

MyAccount.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default connect(
  null,
  mapDispatchToProps
)(withStyles(styles)(withLocalize(MyAccount)));
