import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withLocalize, Translate } from "react-localize-redux";
import siteTranslations from "../translations/site.json";
import heroTranslations from "../translations/hero.json";

import Typography from '@material-ui/core/Typography/Typography';
import Grid from '@material-ui/core/Grid/Grid';
import { showSnackbar } from '../actions/snackbar';

import { setScreen } from '../actions/app';

import TronWeb from 'tronweb';
import Tron from '../utils/tron';

import presetHeroes from '../config/hero';
import presetSkills from '../config/skill';

import AOS from 'aos';
import 'aos/dist/aos.css';

AOS.init({
  once: true, // whether animation should happen only once - while scrolling down
  mirror: false, // whether elements should animate out while scrolling past them
});

const FOUNDATION_ADDRESS = 'TMiT1foMn3D9TysDXeZqVM5Fk4Pbwvfa2d';

class AllHeroes extends Component {
  constructor(props) {
    super(props);

    this.state = {
      purchases: null,
      totalHeroes: 0,
      heroes: [],
      cards: [],
      heroGroups: [],
      lastUpdatedTime: null,
    };
    this.props.addTranslation(siteTranslations);
    this.props.addTranslation(heroTranslations);
  }

  async componentDidMount() {
    await this.initTronWeb();
    await this.loadCards();
    await this.loadCardsGroup();
    window.addEventListener('load', AOS.refresh);
  }

  async initTronWeb() {
    // tronweb

    await new Promise(resolve => {
      const tronWebState = {
        installed: !!window.tronWeb,
        loggedIn: window.tronWeb && window.tronWeb.ready,
      };

      if (tronWebState.installed) {
        this.setState({
          tronWeb: tronWebState,
        });

        return resolve();
      }

      let tries = 0;

      const timer = setInterval(() => {
        if (tries >= 10) {
          const TRONGRID_API = 'https://api.shasta.trongrid.io';

          window.tronWeb = new TronWeb(
            TRONGRID_API,
            TRONGRID_API,
            TRONGRID_API
          );

          this.setState({
            tronWeb: {
              installed: false,
              loggedIn: false,
            },
          });

          clearInterval(timer);
          return resolve();
        }

        tronWebState.installed = !!window.tronWeb;
        tronWebState.loggedIn = window.tronWeb && window.tronWeb.ready;

        if (!tronWebState.installed) return tries++;

        this.setState({
          tronWeb: tronWebState,
        });

        resolve();
      }, 100);
    });

    if (!this.state.tronWeb.loggedIn) {
      // Set default address (foundation address) used for contract calls
      // Directly overwrites the address object as TronLink disabled the
      // function call
      window.tronWeb.defaultAddress = {
        hex: window.tronWeb.address.toHex(FOUNDATION_ADDRESS),
        base58: FOUNDATION_ADDRESS,
      };

      window.tronWeb.on('addressChanged', () => {
        if (this.state.tronWeb.loggedIn) return;

        this.setState({
          tronWeb: {
            installed: true,
            loggedIn: true,
          },
        });
      });
    }

    await Tron.setTronWeb(window.tronWeb);
  }

  getPlayerCardsGroup = async pages => {
    return new Promise(async resolve => {
      let ids = [];
      let protos = [];
      let purities = [];

      for (var i = 0; i < presetHeroes.length; i++) {
        ids.push(presetHeroes[i].id);
        protos.push(presetHeroes[i].id);
        purities.push(0);
      }

      const heroes = {
        ids: ids,
        protos: protos,
        purities: purities,
      };

      this.setState({ heroes });

      resolve(true);
    });
  };

  loadCards = async () => {
    this.setState({ totalHeroes: 30 });

    await this.getPlayerCardsGroup();
  };

  loadCardsGroup = async () => {
    const { heroes } = this.state;

    let cards = heroes;

    let { heroGroups } = this.state;
    if (cards) {
      const ids = cards.ids;
      const protos = cards.protos;
      const purities = cards.purities;
      if(ids){
      for (let i = 0; i < ids.length; i++) {
        let id = ids[i];
        let proto = protos[i];
        let purity = purities[i];
        console.log(proto);
        const heroData = this.getHeroData(proto);
        const rarity = heroData.ra;

        let foundRarityIndex = -1;
        let foundHero = false;
        for (var j = 0; j < heroGroups.length; j++) {
          const heroGroup = heroGroups[j];
          if (heroGroup.rarity === rarity) {
            // found Rarity
            foundRarityIndex = j;
            if (heroGroup.heroes) {
              for (let k = 0; k < heroGroup.heroes.length; k++) {
                const hero = heroGroup.heroes[k];

                if (hero.proto === proto) {
                  // found
                  foundHero = true;

                  if (hero.bestPurity < purity) {
                    hero.bestPurity = purity;
                    hero.bestId = id;
                  }

                  hero.count += 1;
                }
              }
            }
          }
        }

        if (!foundHero) {
          //push card
          const hero = {
            proto: proto,
            bestPurity: purity,
            bestId: id,
            count: 1,
          };

          if (foundRarityIndex > -1) {
            // push to exisiting group
            const heroGroup = heroGroups[foundRarityIndex];
            heroGroup.heroes.push(hero);
          } else {
            let newheroes = [];
            newheroes.push(hero);

            const heroGroup = {
              rarity: rarity,
              heroes: newheroes,
            };
            heroGroups.push(heroGroup);
          }
        } // if hero not found
      } // for i loop

      } // if ids
    } // if cards

    // this.setState({heroes});
    this.setState({ heroGroups });
  };

  Comparator = (a, b) => {
    if (a.rarity < b.rarity) return 1;
    if (a.rarity > b.rarity) return -1;
    return 0;
  };

  reOrderHeroGroups = heroGroups => {
    return heroGroups.sort(this.Comparator);
  };

  getHeroData = id => {
    for (var i = 0; i < presetHeroes.length; i++) {
      if (presetHeroes[i].id === id) {
        return presetHeroes[i];
      }
    }
    return null;
  };

  getHeroSkills = ids => {
    let skills = [];
    if (ids) {
      for (var i = 0; i < ids.length; i++) {
        let id = ids[i];

        for (var j = 0; j < presetSkills.length; j++) {
          if (presetSkills[j].id === id) {
            skills.push(presetSkills[j]);
          }
        }
      }
    }

    return skills;
  };

  getAttackImage = rarity => {
    let image = 'images/misc/sword-special.png';
    switch (rarity) {
      case 0:
        image = 'images/misc/sword-common.png';
        break;
      case 1:
        image = 'images/misc/sword-rare.png';
        break;
      case 2:
        image = 'images/misc/sword-epic.png';
        break;
      case 3:
        image = 'images/misc/sword-legendary.png';
        break;
      case 4:
        image = 'images/misc/sword-mythic.png';
        break;
      default:
        image = 'images/misc/sword-special.png';
        break;
    }

    return image;
  };

  getDefenceImage = rarity => {
    let image = 'images/misc/shield-special.png';
    switch (rarity) {
      case 0:
        image = 'images/misc/shield-common.png';
        break;
      case 1:
        image = 'images/misc/shield-rare.png';
        break;
      case 2:
        image = 'images/misc/shield-epic.png';
        break;
      case 3:
        image = 'images/misc/shield-legendary.png';
        break;
      case 4:
        image = 'images/misc/shield-mythic.png';
        break;
      default:
        image = 'images/misc/shield-special.png';
        break;
    }

    return image;
  };

  getHealthImage = rarity => {
    console.log(rarity);

    let image = 'images/misc/heart-special.png';
    switch (rarity) {
      case 0:
        image = 'images/misc/heart-common.png';
        break;
      case 1:
        image = 'images/misc/heart-rare.png';
        break;
      case 2:
        image = 'images/misc/heart-epic.png';
        break;
      case 3:
        image = 'images/misc/heart-legendary.png';
        break;
      case 4:
        image = 'images/misc/heart-mythic.png';
        break;
      default:
        image = 'images/misc/heart-special.png';
        break;
    }

    return image;
  };
  getHealingImage = rarity => {
    let image = 'images/misc/healing-special.png';
    switch (rarity) {
      case 0:
        image = 'images/misc/healing-common.png';
        break;
      case 1:
        image = 'images/misc/healing-rare.png';
        break;
      case 2:
        image = 'images/misc/healing-epic.png';
        break;
      case 3:
        image = 'images/misc/healing-legendary.png';
        break;
      case 4:
        image = 'images/misc/healing-mythic.png';
        break;
      default:
        image = 'images/misc/healing-special.png';
        break;
    }

    return image;
  };

  getPlateCss = rarity => {
    let css = 'plate-silver';
    switch (rarity) {
      case 0:
        css = 'plate-green';
        break;
      case 1:
        css = 'plate-blue';
        break;
      case 2:
        css = 'plate-pink';
        break;
      case 3:
        css = 'plate-gold';
        break;
      case 4:
        css = 'plate-red';
        break;
      default:
        css = 'plate-silver';
        break;
    }

    return css;
  };

  getSkillCss = rarity => {
    let css = 'skill-silver';
    switch (rarity) {
      case 0:
        css = 'skill-green';
        break;
      case 1:
        css = 'skill-blue';
        break;
      case 2:
        css = 'skill-pink';
        break;
      case 3:
        css = 'skill-gold';
        break;
      case 4:
        css = 'skill-red';
        break;
      default:
        css = 'skill-silver';
        break;
    }

    return css;
  };

  getRarityClass = rarity => {
    let text = 'Common';
    switch (rarity) {
      case 0:
        text = 'Common';
        break;
      case 1:
        text = 'Rare';
        break;
      case 2:
        text = 'Epic';
        break;
      case 3:
        text = 'Legendary';
        break;
      case 4:
        text = 'Mythic';
        break;
      case 5: // This was 4, but it was a bug
        text = 'Special';
        break;
      default:
        text = 'Common';
        break;
    }

    return text;
  };
  getRarityText = rarity => {
    let text = 'general.common';
    switch (rarity) {
      case 0:
        text = 'general.common';
        break;
      case 1:
        text = 'general.rare';
        break;
      case 2:
        text = 'general.epic';
        break;
      case 3:
        text = 'general.legendary';
        break;
      case 4:
        text = 'general.mythic';
        break;
      case 5: // This was 4, but it was a bug
        text = 'general.special';
        break;
      default:
        text = 'general.common';
        break;
    }

    return text;
  };

  getRoleImage = role => {
    let img = 'images/misc/damage.png';
    switch (role) {
      case 0:
        img = 'images/misc/tank.png';
        break;
      case 1:
        img = 'images/misc/damage.png';
        break;
      case 2:
        img = 'images/misc/healer.png';
        break;
      default:
        img = 'images/misc/damage.png';
        break;
    }

    return img;
  };

  renderWalletInfo() {
    const { totalHeroes, heroGroups } = this.state;

    const heroGroupsReordered = this.reOrderHeroGroups(heroGroups);

    let totalCount = 0;
    return (
      <div className="cards">
        <div color="primary" className="card game-card">
          <Typography variant="h4" color="secondary">
            <span className="glowing-pink white"><Translate id="general.allheroes" /></span>
          </Typography>

          <div className="my-account">
            <Grid container spacing={24}>
              <Grid item xs={12} md={12} lg={12}>
                <Typography variant="h6" color="primary">
                  <span className="glowing-pink white">
                    {totalHeroes} <Translate id="general.heroes" />
                  </span>
                </Typography>
                <div className="space" />

                <Grid container spacing={24}>
                  {heroGroupsReordered.map(heroGroup => {
                    // console.log(heroGroup.heroes.length)

                    return heroGroup.heroes.map(hero => {

                      const heroName = `hero.${hero.proto}n`;
                      const heroData = this.getHeroData(hero.proto);
                      const heroImage = `images/hero/${hero.proto}.png`;

                      const attackImage = this.getAttackImage(heroData.ra);
                      const defenceImage = this.getDefenceImage(heroData.ra);
                      const healthImage = this.getHealthImage(heroData.ra);
                      const healingImage = this.getHealingImage(heroData.ra);

                      const skills = this.getHeroSkills(heroData.s);

                      const heroCount = hero.count;

                      totalCount = totalCount + heroCount;

                      let plateCss = this.getPlateCss(heroData.ra);
                      let skillBackgroundCss = this.getSkillCss(heroData.ra);
                      let roleImageSrc = this.getRoleImage(heroData.rl);
                      let raritycla = this.getRarityClass(heroData.ra);
                      let rarityText = this.getRarityText(heroData.ra);
                      let rarityClass = `hero-rarity ${raritycla}`;

                      let bgClass = `bg-${hero.proto}`;

                      const boxCss = `hero-plate card ${plateCss}`;
                      const skillCss = `hero-skill card ${skillBackgroundCss}`;

                      return (
                        <Grid
                          item
                          xs={12}
                          sm={6}
                          md={3}
                          lg={3}
                          className="card"
                        >
                          <div
                            className="hero-placeholder"
                            data-aos="flip-right"
                            data-aos-delay="50"
                            data-aos-anchor-placement="top-center"
                          >
                            <div className="hero-role">
                              <img
                                className="image-skill-small"
                                src={roleImageSrc}
                                alt="hero"
                              />
                            </div>
                            <div className={rarityClass}><Translate id={rarityText} /></div>
                            <div className={boxCss}>
                              <div className={bgClass}>
                                <img
                                  className="image-hero-small"
                                  src={heroImage}
                                  alt="hero"
                                />
                              </div>

                              <div className="space" />
                              <div className="hero-name-banner">
                                <Translate id={heroName} />
                              </div>

                              <div className="hero-stats-plate">
                                {heroData.hl > 0 && (
                                  <Grid container spacing={0}>
                                    <Grid item xs={3} sm={3} md={3} lg={3}>
                                      <div className="hero-stats">
                                        <img
                                          className="image-stats-small"
                                          src={attackImage}
                                          alt="hero"
                                        />
                                        <div className="hero-stats-title">
                                          {heroData.a}
                                        </div>
                                      </div>
                                    </Grid>
                                    <Grid item xs={3} sm={3} md={3} lg={3}>
                                      <div className="hero-stats">
                                        <img
                                          className="image-stats-small"
                                          src={defenceImage}
                                          alt="hero"
                                        />
                                        <div className="hero-stats-title">
                                          {heroData.d}
                                        </div>
                                      </div>
                                    </Grid>
                                    <Grid item xs={3} sm={3} md={3} lg={3}>
                                      <div className="hero-stats">
                                        <img
                                          className="image-stats-small"
                                          src={healthImage}
                                          alt="hero"
                                        />
                                        <div className="hero-stats-title">
                                          {heroData.he}
                                        </div>
                                      </div>
                                    </Grid>
                                    <Grid item xs={3} sm={3} md={3} lg={3}>
                                      <div className="hero-stats">
                                        <img
                                          className="image-stats-small"
                                          src={healingImage}
                                          alt="hero"
                                        />
                                        <div className="hero-stats-title">
                                          {heroData.hl}
                                        </div>
                                      </div>
                                    </Grid>
                                  </Grid>
                                )}

                                {heroData.hl === 0 && (
                                  <Grid container spacing={0}>
                                    <Grid item xs={4} sm={4} md={4} lg={4}>
                                      <div className="hero-stats">
                                        <img
                                          className="image-stats-small"
                                          src={attackImage}
                                          alt="hero"
                                        />
                                        <div className="hero-stats-title">
                                          {heroData.a}
                                        </div>
                                      </div>
                                    </Grid>
                                    <Grid item xs={4} sm={4} md={4} lg={4}>
                                      <div className="hero-stats">
                                        <img
                                          className="image-stats-small"
                                          src={defenceImage}
                                          alt="hero"
                                        />
                                        <div className="hero-stats-title">
                                          {heroData.d}
                                        </div>
                                      </div>
                                    </Grid>
                                    <Grid item xs={4} sm={4} md={4} lg={4}>
                                      <div className="hero-stats">
                                        <img
                                          className="image-stats-small"
                                          src={healthImage}
                                          alt="hero"
                                        />
                                        <div className="hero-stats-title">
                                          {heroData.he}
                                        </div>
                                      </div>
                                    </Grid>
                                  </Grid>
                                )}
                              </div>

                              <div className={skillCss}>
                                <span className="hero-skill-banner">
                                  <Translate id="hero.skill" />
                                </span>
                                {skills.map(skill => {
                                  let imageSrc = `images/skill/${skill.id}.png`;
                                  let skillName = `skill.${skill.id}n`;
                                  let skillDesc = `skill.${skill.id}d`;
                                  return (
                                    <div className="hero-skill-item">
                                      <div className="icon">
                                        <img
                                          className="image-skill-small"
                                          src={imageSrc}
                                          alt="hero"
                                        />
                                      </div>

                                      <div className="description text-shadow">
                                        <div className="title text-shadow">
                                          <Translate id={skillName} />
                                        </div>
                                        <Translate id={skillDesc} />
                                      </div>
                                    </div>
                                  );
                                })}

                                <div className="space" />
                              </div>
                            </div>
                          </div>
                        </Grid>
                      );
                    });
                  })}
                </Grid>
              </Grid>
            </Grid>
          </div>
        </div>
      </div>
    );
  }

  render() {
    return this.renderWalletInfo();
  }
}

const mapDispatchToProps = {
  setScreen,
  showSnackbar,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(AllHeroes));
