class WalletAppBar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      languages:[]
    }

    this.props.initialize({
      languages: [
        { name: "English", code: "en" },
        { name: "", code: "cn" }
      ],
      options: { renderToStaticMarkup }
    });

    this.props.addTranslation(siteTranslations);
    
  }

  async componentDidMount() {


   let languages= [
        { name: "English", code: "en" },
        { name: "", code: "cn" }
      ]
    this.setState({languages});

  }


 setActiveLanguage = (code) => {
    this.props.setActiveLanguage(code);
  };

  getLanguageFlag = (code) => {
    return `images/languages/${code}.png`;
  };

  home = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_PRE_SALE);
  };

  myHeroes = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_MY_HEROES);
  };

  showAllHeroes = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_ALL_HEROES);
  };

  showItems = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_ITEMS);
  };

  showPVE = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_PVE);
  };
  showPVP = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_PVP);
  };

  showProfessions = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_PROFESSIONS);
  };

  showSoulStone = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_SOULSTONE);
  };

  myAccount = () => {
    const { setScreen } = this.props;
    setScreen(SCREEN_MY_ACCOUNT);
  };

  render() {

    const {languages} = this.state;
    return (
      <AppBar position="sticky">
        <Toolbar>
          <div className="tool-bar">
            <div className="logo-container">
              <IconButton
                color="inherit"
                onClick={this.home}
                aria-label=<Translate id="general.tronlegend" />
              >
                <span className="glowing-pink"><Translate id="general.tronlegend" /></span>
              </IconButton>

              <IconButton
                color="inherit"
                onClick={this.showSoulStone}
                aria-label=<Translate id="general.soulstone" />
              >
                <span className="menulink glowing-gold"><img
                          src={imgSS}
                          alt=<Translate id="general.soulstone" />
                          className="image-ss-menu"
                        /><Translate id="general.soulstone" /></span>
              </IconButton>

              <IconButton
                color="inherit"
                onClick={this.showAllHeroes}
                aria-label=<Translate id="general.heroes" />
              >
                <span className="menulink glowing-red"><Translate id="general.heroes" /></span>
              </IconButton>
              <IconButton
                color="inherit"
                onClick={this.showItems}
                aria-label=<Translate id="general.items" />
              >
                <span className="menulink glowing-blue"><Translate id="general.items" /></span>
              </IconButton>
              <IconButton
                color="inherit"
                onClick={this.showPVE}
                aria-label=<Translate id="general.pve" />
              >
                <span className="menulink glowing-blue"><Translate id="general.pve" /></span>
              </IconButton>
              <IconButton
                color="inherit"
                onClick={this.showPVP}
                aria-label=<Translate id="general.pvp" />
              >
                <span className="menulink glowing-blue"><Translate id="general.pvp" /></span>
              </IconButton>

              <IconButton
                color="inherit"
                onClick={this.showProfessions}
                aria-label=<Translate id="general.professions" />
              >
                <span className="menulink glowing-blue"><Translate id="general.professions" /></span>
              </IconButton>
            </div>

            <div>
              <Tooltip title=<Translate id="general.myheroes" />>
                <IconButton
                  color="inherit"
                  onClick={this.myHeroes}
                  aria-label=<Translate id="general.myheroes" />
                >
                  <PermIdentity className="account-details-button-icon" />
                  <span className="menulink glowing-blue"> <Translate id="general.myheroes" /></span>
                </IconButton>
              </Tooltip>

              <Tooltip title=<Translate id="general.myaccount" />>
                <IconButton
                  color="inherit"
                  onClick={this.myAccount}
                  aria-label=<Translate id="general.myaccount" />
                >
                  <PermIdentity className="account-details-button-icon" />
                  <span className="menulink glowing-blue"> <Translate id="general.myaccount" /></span>
                </IconButton>
              </Tooltip>


                {languages.map(lang => (

                  <Tooltip title={lang.name}>
                    <IconButton
                      id={lang.code}
                      color="inherit"
                      onClick={() => this.setActiveLanguage(lang.code)}
                    >
                      <img src={this.getLanguageFlag(lang.code)} className="image-flag" />
                    </IconButton>
                  </Tooltip>

                ))}
              


              
            </div>
          </div>
        </Toolbar>
      </AppBar>
    );
  }
}

const mapDispatchToProps = {
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(WalletAppBar));
