import React from 'react';
import { connect } from 'react-redux';

import {
  SCREEN_PRE_SALE,
  SCREEN_MY_ACCOUNT,
  SCREEN_MY_HEROES,
  SCREEN_SOULSTONE,
  SCREEN_ALL_HEROES,
  SCREEN_ITEMS,
  SCREEN_PVE,
  SCREEN_PVP,
  SCREEN_PROFESSIONS,
} from '../actions/app';
import PreSale from './PreSale';
import MyHeroes from './MyHeroes';
import MyAccount from './MyAccount';
import Heroes from './Heroes';
import SoulStone from './SoulStone';
import Items from './Items';
import PVE from './PVE';
import PVP from './PVP';
import Professions from './Professions';

const RoutingScreen = ({ screen }) => {
  switch (screen) {
    case SCREEN_PRE_SALE:
      return <PreSale />;
    case SCREEN_MY_HEROES:
      return <MyHeroes />;
    case SCREEN_ALL_HEROES:
      return <Heroes />;
    case SCREEN_MY_ACCOUNT:
      return <MyAccount />;
    case SCREEN_ITEMS:
      return <Items />;
    case SCREEN_SOULSTONE:
      return <SoulStone />;
    case SCREEN_PVE:
      return <PVE />;
    case SCREEN_PVP:
      return <PVP />;
    case SCREEN_PROFESSIONS:
      return <Professions />;
    default:
      return null;
  }
};

const mapStateToProps = state => ({
  screen: state.app.screen,
});

export default connect(mapStateToProps)(RoutingScreen);
