  async purchase(amount, packType, referrer) {

    let that = this;

    return new Promise(async function(resolve, reject) {

      let price = 0;

      if(packType == 0)
      {
        price = await that.contract.getBasePrice().call();
      }else if(packType == 1)
      {
        price = await that.contract.getRarePrice().call();
      }else if(packType == 2)
      {
        price = await that.contract.getEpicPrice().call();
      }else if(packType == 3)
      {
        price = await that.contract.getLegendaryPrice().call();
      }


      const packsNumber = amount;
      const payAmount = price.toNumber() * packsNumber;
      try {
        const data = await that.contract
          .purchase(packsNumber, packType, referrer)
          .send({
            feeLimit: gasLimit,
            callValue: payAmount,
            shouldPollResponse: true,
          }).then(function(data){
            resolve(data);
          }).catch(function(err){
            console.log(err)
            reject('Error Occured');
          });
      } catch (e) {
        reject(e);
      }
    });
  },

